/* General styles */
body {
    font-family: "Roboto", Arial, sans-serif;
    /* font-weight: bold; */
    margin: 0;
    padding: 0;
  }
    
  /* Home Section styles */
  #home {
    text-align: center;
    padding: 100px 0;
    padding-top: 180px;
    background-color: #f5f5f5; /* Set a light background color for the home section */
    background: rgba(0, 0, 0, 0.8) url(../images/mountain2.jpg);
    background-size: cover;
    background-blend-mode: darken;
    background-position: center;

  }
  
  .home-img {
    border-radius: 50%;
    width: 160px;
    height: 160px;
    animation: fadeIn 1s, rotateIn 1s; /* Combine fadeIn and rotateIn animations */
  }

  @media (max-width: 768px) {
    .home-img {
      width: 100px;
      height: 100px;
    }
  }
  
  @media (max-width: 576px) {
    .home-img {
      width: 100px;
      height: 100px;
    }
  }
.theme-color{
  color: #f2e9ae;
}
.home-color{
  overflow: hidden;
  border-right: 0.15em solid #333; /* Typing cursor style */
  white-space: nowrap;
  margin: 0 auto;
  letter-spacing: 0.17em; /* Adjust the speed of typing */
  animation: typing 3s steps(40, end) infinite;
}
@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 250px;
  }
}
  /* About Section styles */
  #about {
    padding: 50px;
  }
  
  /* Skills Section styles */
  .skills-card {
    transition: transform 0.2s ease;
    
  }
  .skills-card > div {
    max-width: 200px;
    margin:auto;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  }
  
  .skills-card:hover {
    transform: scale(1.1);
  }
  /* Projects Section styles */
  #projects {
    padding: 50px;
  }
  
  .project-card {
    /* margin-bottom: 30px; */
    /* padding: 20px; */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease;
  }
  
  .project-card:hover {
    transform: scale(1.05);
  }
  
  /* Contact Section styles */
  #contact {
    padding: 50px;
    background-color: #f9f9f9;
  }
  
  /* GitHub Calendar and Stats styles */
  .react-activity-calendar,
  #github-streak-stats,
  #github-top-langs,
  #github-stats-card {
    display: block;
    max-width: 100%;
    margin: 20px auto;
    animation: fadeIn 1s;
  }
  
  /* Animations */
  @keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
  
  /* Responsive Styles */
  @media (max-width: 768px) {
    .skills-card,
    .project-card,
    #github-streak-stats,
    #github-top-langs,
    #github-stats-card {
      width: 100%;
    }
  
    #skills {
      justify-content: center;
    }
  }
  